import { create, SetState } from "zustand";
import { ethers } from 'ethers';

interface StoreState {
  currentItemIndex: number;
  setCurrentItemIndex: (index: number | ((prevIndex: number) => number)) => void;
}

interface WalletState {
  ethersProvider: any | null;
}

interface WalletActions {
  setEthersProvider: (provider: any) => void;
  clearEthersProvider: () => void;
}

interface ModalState {
  isModalOpen: boolean;
  setIsModalOpen: (value: boolean) => void;
}

export const useStore = create<StoreState>((set: SetState<StoreState>) => ({
  currentItemIndex: 0,
  setCurrentItemIndex: (index) => set((state) => ({ currentItemIndex: typeof index === 'function' ? index(state.currentItemIndex) : index })),
}));

export const useWalletStore = create<WalletState & WalletActions>((set) => ({
  ethersProvider: null,
  setEthersProvider: (provider) => set({ ethersProvider: provider }),
  clearEthersProvider: () => set({ ethersProvider: null }),
}));

export const useModalStore = create<ModalState>((set) => ({
  isModalOpen: false,
  setIsModalOpen: (value) => set({ isModalOpen: value })
}));