// EthersProvider.tsx
import React, { ReactNode, createContext, useState, useEffect } from 'react';
import { ethers } from 'ethers';
import { useWalletStore } from 'store/store';
import { useModalStore } from 'store/store';

interface EthersProviderProps {
  children: ReactNode;
}

interface CustomWindow extends Window {
  ethereum?: any;
}

const EthersContext = createContext<any | undefined>(undefined);

const EthersProvider: React.FC<EthersProviderProps> = ({ children }) => {
  const { isModalOpen, setIsModalOpen } = useModalStore();
  const { ethersProvider, setEthersProvider, clearEthersProvider } = useWalletStore();
  const customWindow = window as CustomWindow;

  const checkWalletLoginStatus = async () => {
    if (customWindow.ethereum) {
      try {
        const accounts = await customWindow.ethereum.request({ method: 'eth_accounts' });

        if (accounts.length > 0) {
          const provider = new ethers.providers.Web3Provider(customWindow.ethereum);
          setEthersProvider(provider);
        }
      } catch (error) {
        console.error('MetaMask 연결 상태 확인 중 오류 발생:', error);
      }
    } else {
      setIsModalOpen(true);
    }
  };

  const setupEthersProvider = async () => {
    if (customWindow.ethereum) {
      try {
        await customWindow.ethereum.request({ method: 'eth_requestAccounts' });
        const provider = new ethers.providers.Web3Provider(customWindow.ethereum);

        customWindow.ethereum.on('accountsChanged', () => {
          checkWalletLoginStatus();
        });

        customWindow.ethereum.on('chainChanged', () => {
          // 체인이 변경되었을 때의 처리
        });

        setEthersProvider(provider);
      } catch (error) {
        console.error('MetaMask 연결 요청 중 오류 발생:', error);
      }
    } else {
      window.open("https://metamask.io/download/");
      console.log('MetaMask가 설치되어 있지 않거나 window.ethereum이 정의되지 않았습니다.');
    }
    if(isModalOpen) {
      setIsModalOpen(false);
    } 
  };

  useEffect(() => {
    checkWalletLoginStatus();

    return () => {
      try {
        customWindow.ethereum?.removeAllListeners();
        clearEthersProvider();
      } catch (error) {
        console.error('이벤트 리스너 제거 중 오류 발생:', error);
      }
    };
  }, []);

  const value = ethersProvider ? ethersProvider : null;

  const exportedFunctions = {
    setupEthersProvider,
  };

  return (
    <EthersContext.Provider value={{ ethersProvider, functions: exportedFunctions }}>
      {children}
    </EthersContext.Provider>
  );
};

export { EthersContext, EthersProvider };
